/* eslint-disable class-methods-use-this */
import React from 'react';
import PropTypes from 'prop-types';
import View from './View';
import fb from '../../firebase_service';

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = { authStatus: 'unauthorised', user: null };
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
  }

  login() {
    fb.login().then((result) => {
      const { user } = result;
      const { changePermission } = this.props;
      const status = 'authorised';

      this.setState({ authStatus: 'status', user });
      changePermission(status);
    });
  }

  logout() {
    fb.logout().then(() => {
      const status = 'unauthorised';
      const { changePermission } = this.props;
      console.log('Sign-out successful');
      this.setState({ authStatus: 'unauthorised' });
      changePermission(status);
    });
  }

  render() {
    const { authStatus, user } = this.state;
    return (
      <View data={{ authStatus, user }} onClickLogin={this.login} onClickLogout={this.logout} />
    );
  }
}

Header.propTypes = {
  changePermission: PropTypes.func.isRequired,
};


export default Header;
