import React from 'react';
import PropTypes from 'prop-types';
import { containerStyle, headerButton, headerP } from '../../styles/header';

function View(props) {
  const { data, onClickLogin, onClickLogout } = props;
  const { authStatus, user } = data;

  return (
    <div className="header" style={containerStyle}>
      <h2 style={{ marginLeft: 10 }}>Mentor dashboard</h2>
      {authStatus === 'unauthorised'
        ? <button type="submit" onClick={onClickLogin} style={headerButton}>Sing in</button>
        : (
          <div>
            <img src={user.photoUrl} alt="user" height="100px" width="100px" />
            <p style={headerP}>{user.displayName}</p>
            <button type="submit" onClick={onClickLogout} style={headerButton}>Sing out</button>
          </div>
        )
        }
    </div>
  );
}

View.propTypes = {
  onClickLogout: PropTypes.func.isRequired,
  onClickLogin: PropTypes.func.isRequired,
  data: PropTypes.object.isRequired,
};


export default View;
